.div_w {
  width: 200px;
}
.li_l {
  display: block;
  width: 120px;
  box-sizing: border-box;
}
.thumbnail-image {
  width: 86px;
  position: relative;
  left: 0;
  top: 0;
  overflow: hidden;
  height: 86px;
  border: 1px solid #e2e2e2;
  margin-right: 20px;
  display: inline-block;
  vertical-align: top;
}
.thumbnail-image img {
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
