.div_w {
  width: 200px;
}
.li_l {
  display: block;
  width: 120px;
  box-sizing: border-box;
  text-align: right;
}
.authorization {
  width: 85%;
  text-align: center;
  margin: 0 auto;
}
.authorization p:nth-child(1) {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-bottom: 10px;
  border-bottom: 1px solid #e2e2e2;
  margin-bottom: 10px;
}
.authorization p:nth-child(2) {
  font-size: 12px;
  color: #666666;
}
