.examples {
  width: 100%;
  text-align: center;
}
.examples dt,
.examples dd {
  display: inline-block;
  vertical-align: middle;
  padding: 0;
  margin: 0;
}
.examples dt {
  width: 120px;
  margin-right: 30px;
  box-sizing: border-box;
}
.examples dt img {
  width: 100%;
}
.examples dd p {
  color: #666666;
  font-size: 14px;
  line-height: 25px;
  padding: 0;
  margin: 0;
}
.examples dd p:nth-child(1) {
  color: #333;
}
